import axios from "axios";
import auth from "./auth";

//게시물 목록 요청
async function getBoardList(pageNo=1){
    let response = null;
    try{
        response = await axios.get("/board/list",{params:{pageNo}});
        return {result: "success", data: response.data};
    }catch(error){
        if(error.response){
          //서버 응답이 있다.
          if(error.response.status === 403){
            if(await auth.refreshToken()){
              response = await axios.get("/board/list",{params:{pageNo}});
              return {result: "success", data: response.data};
            }
          }
        }else{
          //네트워크 오류
          return {result:"fail-network"};
        }
    }
   
}

//게시물 쓰기(Mulipart로 구성해서 전달)
async function createBoard(multipartFormData){
    let dbBoard = null;
    try{
        const response = await axios.post("/board/create",multipartFormData);
        dbBoard = response.data;
    }catch(error){
  
        if(error.response){
          //서버 응답이 있다.
          if(error.response.status === 403){
            if(await auth.refreshToken()){
              const response = await axios.post("/board/create",multipartFormData);
              dbBoard = response.data;
            }
          }
        }
    }
    return dbBoard;
}

//게시물 가져오기
async function readBoard(bno, hit) {
    let board = null;
    try {
      const response = await axios.get(`/board/${bno}?hit=${hit}`);
      board = response.data;
    } catch(error) {
      if(error.response){
        //서버 응답이 있다.
        if(error.response.status === 403){
          if(await auth.refreshToken()){
            const response = await axios.get(`/board/${bno}?hit=${hit}`);
            board = response.data;
          }
        }
      }
    }
    return board;
  }

//파일 다운로드
async function downloadBoardAttach(bno){
    let blob = null;

    try {
        const response = await axios.get(`/board/battach/${bno}`,{responseType: "blob"});
        blob = response.data;
      } catch(error) {
        if(error.response){
          //서버 응답이 있다.
          if(error.response.status === 403){
            if(await auth.refreshToken()){
              const response = await axios.get(`/board/battach/${bno}`,{responseType: "blob"});
              blob = response.data;
            }
          }
        }
      }

    return blob;
}

//게시물 삭제
async function deleteBoard(bno){
  let result = null;
  try {
    const response = await axios.delete(`/board/${bno}`);
    result = response.data.result;
  } catch(error) {
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        if(await auth.refreshToken()){
          const response = await axios.delete(`/board/${bno}`);
          result = response.data.result;
        }
      }
    }
  }
  return result;
}

//게시물 수정
async function updateBoard(multipartFormData){
  let dbBoard = null;
  try{
    const response = await axios.put("/board/update",multipartFormData);
    dbBoard = response.data;
  }catch(error){
    if(error.response){
      //서버 응답이 있다.
      if(error.response.status === 403){
        if(await auth.refreshToken()){
          const response = await axios.put("/board/update",multipartFormData);
          dbBoard = response.data;
        }
      }
    }
  }
  return dbBoard;
}


//내보내기
export default{
    getBoardList,
    createBoard,
    readBoard,
    downloadBoardAttach,
    deleteBoard,
    updateBoard,
}